.feedback {
  background: url("../img/bg-feedback-desktop.png") center / cover no-repeat;

  @include vp-1023 {
    background: none;
  }
}

.feedback__container {
  max-width: 1260px;
  margin: 0 auto;
  padding: 170px 20px 230px 20px;
  display: flex;
  justify-content: flex-end;
  flex-wrap: wrap;
  background: url("../img/paper-airplane-desktop.png") center left / 510px 285px no-repeat;

  @include vp-1023 {
    background: none;
    background-color: $color-neon-blue;
    padding: 50px 50px 105px;
  }

  @include vp-767 {
    padding: 50px 25px 70px;
  }
}

.feedback__form {
  min-width: 580px;
  max-width: 50%;

  @include vp-1023 {
    min-width: 100%;
  }
}

.form__content h2 {
  font-family: $ff-main;
  color: $color-default-white;
  font-size: 36px;
  font-weight: 400;
  line-height: 50px;
  margin: 0;
  margin-bottom: 15px;

  @include vp-767 {
    font-size: 30px;
    line-height: 42px;
    margin-bottom: 20px;
  }
}

.form__content p {
  color: $color-default-white;
  margin: 0;
  margin-bottom: 35px;

  @include vp-767 {
    font-size: 14px;
    line-height: 18px;
    margin-bottom: 40px;
  }
}

.form__field {
  margin: 0;
  margin-left: 5px;
  max-width: 580px;
  border: none;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 30px;
  margin-bottom: 20px;

  @include vp-767 {
    grid-template-columns: 1fr;
    gap: 20px;
  }

  @include vp-1023 {
    margin-left: 0;
    max-width: 100%;
  }
}

.form__field input {
  background-color: rgba(4, 20, 39, 0.15);
  padding: 15px;
  border: none;
  color: $color-default-white;
  font-family: $ff-secondary;
  font-size: 16px;
  line-height: 21px;
  font-weight: 400;
  width: 100%;
  max-width: 275px;
  border-radius: 3px;

  &::placeholder {
    color: #ffffff;
  }

  @include vp-1023 {
    max-width: none;
  }

  @include vp-767 {
    max-width: none;
  }
}

.form textarea {
  background-color: rgba(4, 20, 39, 0.15);
  border: none;
  border-radius: 3px;
  color: $color-default-white;
  font-family: $ff-secondary;
  font-size: 16px;
  line-height: 21px;
  font-weight: 400;
  padding: 25px 15px;
  max-width: 580px;
  width: 100%;
  min-height: 155px;
  margin-left: 5px;
  margin-bottom: 35px;

  &::placeholder {
    color: #ffffff;
  }

  @include vp-1023 {
    margin-left: 0;
    min-width: 100%;
  }
}

.form__footer {
  display: grid;
  grid-template-columns: 265px auto;
  gap: 50px;
  align-items: center;
  margin-left: 5px;

  @include vp-1023 {
    gap: 85px;
    margin-left: 0;
  }

  @include vp-767 {
    grid-template-columns: 100%;
    gap: 25px;
  }
}

.form__checkbox input {
  cursor: pointer;
  position: absolute;
  opacity: 0;
  height: 0;
  width: 0;
}

.form__checkbox-label {
  position: relative;
  cursor: pointer;
  display: flex;
  align-items: center;
}

.form__checkbox-text {
  color: $color-default-white;
  font-size: 14px;
  line-height: 16px;
  font-weight: 400;

  @include vp-1023 {
    max-width: 230px;
  }
}

.form__checkbox-checkmark {
  position: relative;
  display: block;
  width: 15px;
  min-width: 15px;
  height: 15px;
  box-sizing: border-box;
  background-color: $color-transparent;
  border: 1px solid #ffffff;
  border-radius: 2px;
  margin-right: 10px;
}

.form__checkbox-checkmark::after {
  content: "";
  position: absolute;
  display: none;
  left: 2px;
  top: 3px;
  width: 8px;
  height: 5px;
  background-image: url("../img/svg/checkbox.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 8px 5px;
}

.form__checkbox-label input:checked ~ .form__checkbox-checkmark::after {
  display: block;
}
